import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    theme: true
}

const displaySlice = createSlice({
    name:"theme",
    initialState,
    reducers:{
        changeTheme(state){
            state.theme =! state.theme
        }
    }
})

export default displaySlice.reducer;
export const displayActions=displaySlice.actions;