import { Fragment, memo, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import CourseCardComponent from "../components/CourseCardComponent";
import axios from "axios";
import { btnColorOption } from "../partials/BtnPartial";
import ListViewComponent from "../components/ListViewComponent";
import BtnPage from "../partials/BtnPage";
import BtnDisplay from "../partials/BtnDisplay";
import ROUTES from "../routes/routes";

const HomePage = () => {
  const [courseArray, setCourseArray] = useState(null);
  const isAdmin = useSelector((state) => state.myAuth.userInfo.isAdmin);
  const userInfo = useSelector((state) => state.myAuth.userInfo);
  const theme = useSelector((state) => state.myDisplay.theme);
  const [grid, setGrid] = useState(true);
  const [pageNum, setPageNum] = useState(1);

  const navigate = useNavigate();

  useEffect(() => {
    axios
      .get(`/${pageNum}`)
      .then(({ data }) => {
        setCourseArray(data);
      })
      .catch((error) => console.log(error));
  }, [pageNum]);

  const handleAddToWishList = async (id) => {
    try {
      if (!userInfo.id) {
        navigate(ROUTES.LOGIN);
      } else {
        const { data } = await axios.post("/users/wish", {
          productID: id,
        });
        toast(`${data.msg}`);
      }
    } catch (error) {
      let {data} = error.response;

      let {details} = data.err
      toast(details[0])
  
    }
  };

  const handleOrderClick = async (id, copyNumber, privetNumber) => {
    try {
      if (!userInfo.id) {
        navigate(ROUTES.LOGIN);
      } else {
        let validatedData = {
          copiesNeeded: copyNumber,
          privetLesson: privetNumber,
          courseID: id,
          userID: userInfo.id,
        };
        const { data } = await axios.post("/users/order", { ...validatedData });

        toast(`${data.toUser}`);
      }
    } catch (error) {
      const { err } = error.response.data;
      let userMsg = err.filed[0];
      toast(userMsg);
    }
  };

  const handleEditClick = (id) => {
    navigate(`/edit/${id}`);
  };



  // const handleEmailClick = (id) => {
  //   console.log("email clicked");
  // };

  const handleDeleteClick = async (id) => {
    try {
      const deleteAction = await axios.delete("admin/deleteCourse", {
        data: { productID: id },
      });

      toast(deleteAction.data.msg);
      await axios
        .get(`/${pageNum}`)
        .then(({ data }) => {
          setCourseArray(data);
        })

        .catch((error) => console.log(error));
    } catch (error) {
      console.log(error);
    }
  };

  const nextPage = () => {
    setPageNum((prevState) => prevState + 1);
  };

  const previousPage = () => {
    setPageNum((prevState) => prevState - 1);
  };

  const changeLayout = () => {
    setGrid((state) => (state = !state));
  };

  if (!courseArray || courseArray.length <= 0) {
    return (
      <div className="spinner-border text-primary" role="status">
        <span className="visually-hidden">Loading...</span>
      </div>
    );
  } else {
    return (
      <Fragment>
        <div
          className={
            theme
              ? `container-fluid px-5 text-center pt-2`
              : `container-fluid px-5 text-center pt-2`
          }
        >
          <div className="d-flex justify-content-end">
            <BtnDisplay
              btnColor={
                theme ? btnColorOption.secondary : btnColorOption.warning
              }
              onClick={changeLayout}
            >
              {"Display"}
            </BtnDisplay>
          </div>
          {grid ? (
            <div className="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-4">
              {courseArray.map((course) => (
                <div className="col" key={course._id + Date.now()}>
                  <CourseCardComponent
                    courseName={course.courseName}
                    lecturer={course.lecturer}
                    category={course.category}
                    description={course.description}
                    price={course.price}
                    id={course._id}
                    onAddToWishList={handleAddToWishList}
                    onOrder={handleOrderClick}
                    isAdmin={isAdmin}
                    onDelete={handleDeleteClick}
                    onEdit={handleEditClick}
                    courseImg={course.courseImg}
                    // onEmail={handleEmailClick}
                    lecturerEmail={course.lecturerEmail}
                  />
                </div>
              ))}
            </div>
          ) : (
            <Fragment>
              {courseArray.map((course) => (
                <div className="col- mt-1 mb-2" key={course._id + Date.now()}>
                  <ListViewComponent
                    courseName={course.courseName}
                    lecturer={course.lecturer}
                    category={course.category}
                    description={course.description}
                    price={course.price}
                    id={course._id}
                    onAddToWishList={handleAddToWishList}
                    onOrder={handleOrderClick}
                    isAdmin={isAdmin}
                    onDelete={handleDeleteClick}
                    onEdit={handleEditClick}
                    courseImg={course.courseImg}
                  />
                </div>
              ))}
            </Fragment>
          )}

          <div className="p-4">
            <BtnPage btnColor={btnColorOption.secondary} onClick={previousPage}>
              Previous Page
            </BtnPage>
            <BtnPage btnColor={btnColorOption.secondary} onClick={nextPage}>
              Next Page
            </BtnPage>
          </div>
        </div>
      </Fragment>
    );
  }
};

export default memo(HomePage);
