// imports
import PropTypes from "prop-types";
import { memo, useCallback } from "react";

// deleration
const btnColorOption = {
  primary: "primary",
  secondary: "secondary",
  success: "success",
  danger: "danger",
  warning: "warning",
  info: "info",
  light: "light",
  dark: "dark",
};

const BtnDisplay = ({ btnColor, children, icon, onClick }) => {
  const defaultOption = btnColorOption.primary;
  let btnFunc = useCallback(()=>onClick(),[onClick])


  return (
    <button
    id="listBtn"
      className={` btn btn-outline-${btnColor ? btnColor : defaultOption}`} onClick={btnFunc} >
      {icon && <i className={` bi bi-${icon} listIcon`}></i>
     }

      {children}
    </button>
  );
};

BtnDisplay.propTypes = {
  btnColor: PropTypes.oneOf(Object.values(btnColorOption)),
  children: PropTypes.string,
  onClick: PropTypes.func,
  icon: PropTypes.string
};

BtnDisplay.defaultProp = {
  btnColor: btnColorOption.secondary,
};

export { btnColorOption };
export default memo(BtnDisplay);
