import Joi from "joi";
import validate from "./validate";

const orderSchema = Joi.object({
  copiesNeeded: Joi.number().min(0).max(100),
  privetLesson: Joi.number().min(0).max(100),
  courseID:Joi.string()
  .min(24)
  .max(200)
  .alphanum()
  .required()
  .trim()
  .messages({ "string.base": "courseID Missing / Invalid filed" }),
  // userID: Joi.string()
  // .min(24)
  // .max(200)
  // .alphanum()
  // .required()
  // .trim()
  // .messages({ "string.base": "userID Missing / Invalid filed" }),
});


const orderSchemaValidation = (userInput)=>{
    return validate(orderSchema, userInput)

}

export default orderSchemaValidation;