import Joi from "joi";
import validate from "./validate";

const editCourseSchema = Joi.object({
    productID:Joi.string().min(3).max(200).required() ,
    filedToUpdate:Joi.string().min(3).max(200).required(),
    dataUpdated:Joi.string().min(1).max(200).required()
})

const editCourseSchemaValidation = (userInput)=>{
    return validate(editCourseSchema, userInput)

}

export default editCourseSchemaValidation;