import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import { memo } from "react";
import PropTypes from "prop-types";


const NavLinkComponent = ({ to, children , onClick, userIMG=null}) => {
  const theme = useSelector((state) => state.myDisplay.theme);

  return (
    <NavLink 
    to={`${to}`} 
    className={` nav-link ps-2`} 
    style={ theme ? {color:"#18122B"} :  {color:"  white"}}
    onClick={onClick}>
      {userIMG ? <img src={userIMG} alt="" /> : children}
    </NavLink>
  );
};


NavLinkComponent.propTypes = {
  to:PropTypes.string,
  userIMG:PropTypes.string,
  onClick: PropTypes.func,
  children: PropTypes.any
};

export default memo(NavLinkComponent);
