import {  useParams, useNavigate } from "react-router-dom";
import { Fragment, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import CourseCardComponent from "../components/CourseCardComponent";
import axios from "axios";
import ROUTES from "../routes/routes";

const CategoryPage = () => {
  const { category } = useParams();
  const [courseArray, setCourseArray] = useState(null);
  const isAdmin = useSelector((state) => state.myAuth.userInfo.isAdmin);
  const userInfo = useSelector((state) => state.myAuth.userInfo);


  const navigate = useNavigate();

  useEffect(() => {
    axios
      .get(`/category/${category}`)
      .then(({ data }) => {
        console.log(data)
        if (data.err) {
          let err = data.err;
          const msgToUser = err.filed[0];
          toast(msgToUser);
          navigate(ROUTES.HOME);
        } else {
          setCourseArray(data);
        }
      })
      .catch((error) => console.log(error));
  }, [category]);

  const handleAddToWishList = async (id) => {
    try {
      const { data } = await axios.post("/users/wish", {
        productID: id,
      });
      toast(`${data.msg}`);
    } catch (error) {
      let err;
      if (error.response.data.message) {
        err = error.response.data.message;
        toast(`${err}`);
      } else {
        err = error.response.data.err.filed;
        toast(`${err}`);
      }
    }
  };

  const handleOrderClick = async (id, copyNumber, privetNumber) => {
    try {
      // validation in card

      const { data } = await axios.post("/users/order", {
        courseID: id,
        userID: userInfo.id,
        copiesNeeded: copyNumber,
        privetLesson: privetNumber,
      });

      toast(`${data.toUser}`);

      // complate - need to navigate to see te order
    } catch (error) {
      let err;
      if (error.response.data.message) {
        err = error.response.data.message;
        toast(`${err}`);
      } else {
        err = error.response.data.err.filed;
        toast(`${err}`);
      }
    }
  };

  const handleEditClick = (id) => {
    navigate(`/edit/${id}`);
  };

  const handleDeleteClick = async (id) => {
    try {
      const deleteAction = await axios.delete("admin/deleteCourse", {
        data: { productID: id },
      });

      toast(deleteAction.data.msg);
      axios.get(`/category/${category}`)
      .then(({ data }) => {
        if (data.err) {
          let err = data.err;
  
          const msgToUser = err.filed[0];
          toast(msgToUser);
          navigate(ROUTES.HOME);
        } else {
          setCourseArray(data);
        }
      })
      .catch((error) => console.log(error));

    } catch (error) {
      console.log(error);
    }
  };

  if (!courseArray) {
    return (
      <div className="spinner-border text-primary" role="status">
        <span className="visually-hidden">Loading...</span>
      </div>
    );
  } else {
    return (
      <Fragment>
        <div className="container text-center">
          <div className="row row-cols-1 row-cols-md-3 g-4">
            {courseArray.map((course) => (
              <div className="col" key={course._id + Date.now()}>
                <CourseCardComponent
                  courseName={course.courseName}
                  lecturer={course.lecturer}
                  category={course.category}
                  description={course.description}
                  price={course.price}
                  id={course._id}
                  onAddToWishList={handleAddToWishList}
                  onOrder={handleOrderClick}
                  isAdmin={isAdmin}
                  onDelete={handleDeleteClick}
                  onEdit={handleEditClick}
                  courseImg={course.courseImg}
                />
              </div>
            ))}
          </div>
          <div className="p-4">
            {/* <BtnPartial btnColor={btnColorOption.secondary} onClick={previousPage}>Previous Page</BtnPartial>
            <BtnPartial btnColor={btnColorOption.secondary} onClick={nextPage}>Next Page</BtnPartial> */}
            {/* <BtnPartial onClick={getTopic}>topic</BtnPartial> */}
          </div>
        </div>
      </Fragment>
    );
  }
};

export default CategoryPage;
