import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { toast } from "react-toastify";
import BtnPartial from "../partials/BtnPartial";
import resetSchemaValidation from "../validation/rsetValidation";
import AlertPartial from "../partials/AlertPartial";
import axios from "axios";
import { btnColorOption } from "../partials/BtnPartial";

const ResetPasswordPage = () => {

  const [errors, setErrors] = useState(null);
  const navigate = useNavigate();

  const [resetPasswordInput, setResetPasswordInputs] = useState({
    email: "",
    password: "",
    newPassword: "",
    phone: "",
  });

  const formOnChange = (event) => {
    let resetInputsCopy = JSON.parse(JSON.stringify(resetPasswordInput));
    resetInputsCopy[event.target.id] = event.target.value;
    setResetPasswordInputs(resetInputsCopy);
  };

  const submitFrom = async (event) => {
    event.preventDefault();
    try {
      setErrors({});
      const validateUserInput = resetSchemaValidation(resetPasswordInput);
      if (validateUserInput) {
        setErrors(validateUserInput);
      } else {
        const { data } = await axios.post("/forgotPass", {
          email: resetPasswordInput.email,
          newPassword: resetPasswordInput.newPassword,
          phone: resetPasswordInput.phone,
        });
        const msgToUser = data.msg;
        toast(msgToUser);
        navigate("/");
      }
    } catch (error) {
      console.log(error);
    }
  };


  return (
    <div
      className={`container-fluid  p-5 pt-2`}
    >
      <form className="pt-3 pb-5">
        <div className="d-flex  justify-content-center">
          <div className="form-group col-md-6 mt-3 mb-2">
            <label className="py-1" htmlFor="email">
              Email address
            </label>
            <input
              type="email"
              className="form-control"
              id="email"
              aria-describedby="emailHelp"
              placeholder="Enter email"
              value={resetPasswordInput.email}
              onChange={formOnChange}
            />
            {errors && errors.email && (
              <AlertPartial>{errors.email}</AlertPartial>
            )}
          </div>
        </div>

        <div className="d-flex  justify-content-center">
          <div className="form-group col-md-6 mt-2 mb-2">
            <label className="py-1" htmlFor="newPassword">
              Current Password
            </label>
            <input
              type="password"
              className="form-control"
              id="password"
              placeholder="Password"
              value={resetPasswordInput.password}
              onChange={formOnChange}
            />
            {errors && errors.password && (
              <AlertPartial>{errors.password}</AlertPartial>
            )}
          </div>
        </div>

        <div className="d-flex  justify-content-center">
          <div className="form-group col-md-6 mt-2 mb-2">
            <label className="py-1" htmlFor="newPassword">
              New Password
            </label>
            <input
              type="password"
              className="form-control"
              id="newPassword"
              placeholder="Password"
              value={resetPasswordInput.newPassword}
              onChange={formOnChange}
            />
            {errors && errors.newPassword && (
              <AlertPartial>{errors.newPassword}</AlertPartial>
            )}
          </div>
        </div>

        <div className="d-flex  justify-content-center ">
          <div className="form-group col-md-6 mt-2 mb-2">
            <label htmlFor="phone" className="py-1">
              Phone
            </label>
            <input
              type="string"
              className="form-control"
              id="phone"
              placeholder="phoneNumber"
              value={resetPasswordInput.phone}
              onChange={formOnChange}
            />
            {errors && errors.phone && (
              <AlertPartial>{errors.phone}</AlertPartial>
            )}
          </div>
        </div>

        <div className="d-flex  justify-content-center mt-4">
          <BtnPartial btnColor={btnColorOption.secondary} onClick={submitFrom}>
            Set New Password
          </BtnPartial>
        </div>
      </form>
    </div>
  );
};

export default ResetPasswordPage;
