import React, {useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { btnColorOption } from "../../partials/BtnPartial";
import LinkClass from "../../classes/LinkClaas";
import NavLinkComponent from "../NavLinkComponent";
import { useDispatch, useSelector } from "react-redux";
import { displayActions } from "../../store/theme";
import { authAction } from "../../store/auth";
import BtnPartialMode from "../../partials/BtnPartialMode";
import ROUTES from "../../routes/routes";
import BtnSearch from "../../partials/BtnSearch";



// data
const rightSideLink = [
  new LinkClass("Home", "/"),
  new LinkClass("About", "/About"),
  new LinkClass("Contact", "/Contact"),
];

const leftSideLink = [
  new LinkClass("Register", "/Register"),
  new LinkClass("Login", "/Login"),
];

const adminLink = [
  new LinkClass("Home", "/"),
  new LinkClass("Add Course", "/add"),
  new LinkClass("Add Admin", "/addAdmin"),
  new LinkClass("Users Management", "/editUser"),
];

const NavbarComponent = () => {
  const theme = useSelector((state) => state.myDisplay.theme);
  const userLogged = useSelector((state) => state.myAuth.isLogged);
  const userInfo = useSelector((state) => state.myAuth.userInfo);
  const isAdmin = userInfo.isAdmin;
  const isVip = userInfo.isVip;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [searchState, setSearchState] = useState({
    searchInput: "",
  });


  const handleSearchChange = (event) => {
    let searchStateCopy = JSON.parse(JSON.stringify(searchState));
    searchStateCopy[event.target.id] = event.target.value;
    setSearchState(searchStateCopy);
  };

  const handleSearchClick = (event) => {
    setSearchState((prevState) => prevState);
    navigate(`/showByCategory/${searchState.searchInput}`);
  };

 
  const handleClick = () => {
    dispatch(displayActions.changeTheme());
  };

  const handleLogoutClick = () => {
    dispatch(authAction.userLoggedOut());
  };

  let color = {
    bgLnav: "bgLnav",
    bgDnav: "bgDnav",
    textLnav: "textLnav",
    textDnav: "textDnav",
  };

  return (
    <nav
      className={
        theme
          ? `navbar navbar-expand-lg bg-body-tertiary navbar ${color.bgLnav} ${color.textDnav}`
          : `navbar navbar-expand-lg bg-body-tertiary navbar  ${color.bgDnav} ${color.textLnav}`
      }
    >
   
        <Link className={`navbar-brand ms-2  `} to={ROUTES.ABOUT}>
           <i className="bi bi-emoji-laughing navIcon mx-2"> </i>
        </Link>
        <button
          className="me-2 navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            {isAdmin
              ? [
                  adminLink.map((navItem) => {
                    return (
                      <li
                        className="nav-item textColor"
                        key={Math.random() + Date.now()}
                      >
                        <NavLinkComponent
                          to={navItem.to}
                          textColor={
                            theme ? `${color.textDnav}` : `${color.textLnav}`
                          }
                        >
                          {navItem.name}
                        </NavLinkComponent>
                      </li>
                    );
                  }),
                ]
              : [
                  rightSideLink.map((navItem) => {
                    return (
                      <li className="nav-item" key={Math.random() + Date.now()}>
                        <NavLinkComponent
                          to={navItem.to}
                          textColor={
                            theme ? `${color.textDnav}` : `${color.textLnav}`
                          }
                        >
                          {navItem.name}
                        </NavLinkComponent>
                      </li>
                    );
                  }),
                ]}
          </ul>

          <form className="d-flex" role="search">
            <input
              className="form-control mx-2"
              type="search"
              placeholder="Search"
              aria-label="Search"

              id="searchInput"
              value={searchState.searchInput}
              onChange={handleSearchChange}
            />
            <BtnSearch
              // btnColor={colorState.search}
              onClick={(event)=>{
                event.preventDefault()
                handleSearchClick()
              }}
            >
              Search
            </BtnSearch>
          </form>
          <ul className="navbar-nav  mb-2 mb-lg-0">
            {userLogged
              ? [ 
                  <li className="nav-item" key={Math.random() + Date.now()}>
                    <NavLinkComponent
                      to={"/profile"}
                      // textColor={colorState.navText}
                      textColor={color.textDnav}
                      // textColor={theme ? `${color.textDnav}`: `${color.textLnav}`}
                    >
                      { isVip ?  <img className="nanIMG" src={userInfo.userIMG} alt="" /> : userInfo.name}
                      {/* {userInfo.name} */}
                    </NavLinkComponent>
                  </li>,
                  <li className="nav-item" key={Math.random() + Date.now()}>
                    <NavLinkComponent
                      to={"/Login"}
                      // textColor={colorState.navText}
                      textColor={
                        theme ? `${color.textDnav}` : `${color.textLnav}`
                      }
                      onClick={handleLogoutClick}
                    >
                      {"logout"}
                    </NavLinkComponent>
                  </li>,
                ]
              : leftSideLink.map((navItem) => {
                  return (
                    <li className="nav-item" key={Math.random() + Date.now()}>
                      <NavLinkComponent
                        to={navItem.to}
                        textColor={
                          theme ? `${color.textDnav}` : `${color.textLnav}`
                        }
                      >
                        {navItem.name}
                      </NavLinkComponent>
                    </li>
                  );
                })}
            <BtnPartialMode
              btnColor={btnColorOption.light}
              onClick={handleClick}
            >
              {theme ? (
                <i className="bi bi-moon navIcon"></i>
              ) : (
                <i className="bi bi-brightness-high navIcon"></i>
              )}
            </BtnPartialMode>
          </ul>
        </div>
    </nav>
  );
};

export default NavbarComponent;
