import { useSelector } from "react-redux";

const FooterCop = () => {
  const theme = useSelector((state) => state.myDisplay.theme);

  let color = {
    bgL: "bgL",
    bgD: "bgD",
    textL: "textL",
    textD: "textD",
  };

  return (
    <div
      className={
        theme
          ? `container  ${color.bgL} ${color.textD}`
          : `container ${color.bgD}  ${color.textL}`
      }
    >
      <div className={theme ? `row borderD` : `row borderL`}>
        <div className="col-sm-12 d-flex justify-content-center mt-4 ">
          <p>
            <i className="px-2 pt-3 bi bi-facebook"></i>
          </p>
          <p>
            <i className="px-2 pt-3 bi bi-instagram"></i>
          </p>
          <p>
            <i className="px-2 pt-3 bi bi-linkedin"></i>
          </p>
        </div>
      </div>

      {/*  */}

      <div className="row">
        <div className="col-sm-12 d-flex justify-content-center ">
          <p>
            &copy; All Copyright Reserved Gerard Trigalo
          </p>
        </div>
      </div>
    </div>
  );
};

export default FooterCop;
