import { useState } from "react";
import axios from "axios";
import BtnPartial from "../partials/BtnPartial";
import { btnColorOption } from "../partials/BtnPartial";


const UnblockPage = () => {
  const [unblockInputs, setUnblockInputs] = useState({
    adminEmail: "",
    adminPassword: "",
    userUnblockEmail: "",
  });

  const formOnChange =  (event) => {
    let copyInputs = JSON.parse(JSON.stringify(unblockInputs))
    copyInputs[event.target.id] = event.target.value;
    setUnblockInputs(copyInputs)
  };

  const formSubmit = async (event) => {
    event.preventDefault();
    try {
        await axios.patch("/admin/unblock", {
            email:unblockInputs.adminEmail,
            password:unblockInputs.adminPassword,
            userUnblockEmail:unblockInputs.userUnblockEmail
        })
    }catch(error){
        return error
    }


  };

  return (
    <form >
      <div className="form-group">
        <label htmlFor="adminEmail">Email address</label>
        <input
          type="email"
          className="form-control"
          id="adminEmail"
          aria-describedby="emailHelp"
          placeholder="Enter email"
          value={unblockInputs.adminEmail}
          onChange={formOnChange}
        />
      </div>
      <br />
      <div className="form-group">
        <label htmlFor="adminPassword">Password</label>
        <input
          type="password"
          className="form-control"
          id="adminPassword"
          placeholder="Password"
          value={unblockInputs.adminPassword}
          onChange={formOnChange}
        />
      </div>
      <br />

      <div className="form-group">
        <label htmlFor="userUnblockEmail">Email address</label>
        <input
          type="email"
          className="form-control"
          id="userUnblockEmail"
          aria-describedby="emailHelp"
          placeholder="Enter email"
          value={unblockInputs.userUnblockEmail}
          onChange={formOnChange}
        />
      </div>
      <br />
      {/* <button type="submit" className="btn btn-primary">
        Submit
      </button> */}
      <BtnPartial btnColor={btnColorOption.secondary} onClick={formSubmit}>Unblock</BtnPartial>
    </form>
  );
};

export default UnblockPage;
