import { useState } from "react";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import unblockUserSchemaValidation from "../validation/unblockUserValidation";
import editUserSchemaValidation from "../validation/editUserValidation";
import deleteUserSchemaValidation from "../validation/deleteUserValidation";
import AlertPartial from "../partials/AlertPartial";
import BtnPartial from "../partials/BtnPartial";
import { btnColorOption } from "../partials/BtnPartial";
import axios from "axios";

const UserManagementPage = () => {
  const userInfoTOken = useSelector((state) => state.myAuth.userInfo);
  const [errors, setErrors] = useState(null);

  const [userInfo, setUserInfo] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    country: "",
    city: "",
    street: "",
    houseNumber: "",
    zip: "",
    originalEmail: "",
    isVip: "",
    userIMG: "",
  });

  const onChangeInput = (event) => {
    let userInfoInputsCopy = JSON.parse(JSON.stringify(userInfo));
    userInfoInputsCopy[event.target.id] = event.target.value;
    if(userInfoInputsCopy.isVip ==="false"){
      userInfoInputsCopy.userIMG = ""
    } else {
      userInfoInputsCopy[event.target.id] = event.target.value;
    }
    setUserInfo(userInfoInputsCopy);
    setErrors({});
  };

  // מקבל מידע מהשרת על היוזר שנרצה לעדכן, ומעדכן את הסטייט כדי הציג נתונים

  const getUserData = async (event) => {
    event.preventDefault();
    let userEmail = userInfo.email;
    try {
      setErrors({});
      const validateUserData = deleteUserSchemaValidation({ email: userEmail });
      if (validateUserData) {
        setErrors(validateUserData);
      } else {
        const { data } = await axios.get(`/admin/getInfoEmail/${userEmail}`);
        console.log("data", data);

        let userDataFromServer = {
          firstName: data.name,
          lastName: data.lastName,
          email: data.email,
          phone: data.phone,
          country: data.address.country,
          city: data.address.city,
          street: data.address.street,
          houseNumber: data.address.houseNumber,
          zip: data.address.zip,
          originalEmail: data.email,
          userIMG: data.userIMG,
          isVip: data.isVip,
        };
        setUserInfo(userDataFromServer);
      }
    } catch (error) {
      const { response } = error;
      const { data } = response;
      const msgToClient = data.err.filed[0];

      toast(msgToClient);
    }
  };

  const updateUserData = async (event) => {
    event.preventDefault();
    try {
      const {
        email: userEmail,
        originalEmail: newEmail,
        ...restUserInfoFiled
      } = userInfo;
      

      let userInfoUpdated = { userEmail, newEmail, ...restUserInfoFiled };
     
      console.log("new data: ", userInfoUpdated);

      const validateUserInput = editUserSchemaValidation(userInfoUpdated);
      if (validateUserInput) {
        setErrors(validateUserInput);
      } else {
        let dataToServer = {
          firstName: userInfoUpdated.firstName,
          lastName: userInfoUpdated.lastName,
          userEmail: userInfo.originalEmail,
          newEmail: userInfo.email,
          phone: userInfoUpdated.phone,
          address: {
            country: userInfoUpdated.country,
            city: userInfoUpdated.city,
            street: userInfoUpdated.street,
            houseNumber: userInfoUpdated.houseNumber,
            zip: userInfoUpdated.zip,
          },
          isVip: userInfoUpdated.isVip,
          userIMG: userInfoUpdated.userIMG,
        };
        const { data } = await axios.put("users/editUser", { ...dataToServer });
        console.log("data: ", data)
        let msg = data.msg;
        toast(msg);
      }
    } catch (error) {
      console.log("error: ", error)
      const { response } = error;
      const { data } = response;
      let { filed } = data.error;
      toast(filed[0]);
    }
  };

  const deleteUserByEmail = async (event) => {
    event.preventDefault();
    const userEmail = userInfo.email;
    try {
      setErrors({});
      const validateUserData = deleteUserSchemaValidation({ email: userEmail });
      if (validateUserData) {
        console.log("error validation")
        setErrors(validateUserData);
      } else {
        const { data } = await axios.delete(
          `admin/deleteUserByEmail/${userEmail}`
        );
        const msgToUser = data.msg;
        toast(msgToUser);
      }
    } catch (error) {
      const { response } = error;
      const { data } = response;
      const msgToClient = data.err.filed[0];

      toast(msgToClient);
    }
  };

  const unblockUser = async (event) => {
    event.preventDefault();
    try {
      const adminEmail = userInfoTOken.email;
      let unblockObg = {
        email: userInfo.email,
        adminEmail,
      };
      setErrors({});
      const validateData = unblockUserSchemaValidation(unblockObg);
      if (validateData) {
        setErrors(validateData);
      } else {
        const { data } = await axios.patch(`admin/unblock`, {
          userUnblockEmail: unblockObg.email,
          email: adminEmail,
        });
        const msgToUser = data.msg;
        toast(msgToUser);
      }
    } catch (error) {
      const { response } = error;
      const { data } = response;
      const msgToClient = data.err.filed[0];

      toast(msgToClient);
    }
  };

  return (
    <form className={"p-5 pt-2"}>
      <div>
        <div className="row mt-2">
          <div className="form-group col-md-12 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="email" className="form-label mt-3">
                Email address
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>
            <input
              type="email"
              className="form-control"
              id="email"
              aria-describedby="emailHelp"
              value={userInfo.email}
              onChange={onChangeInput}
            />
            {errors && errors.email && (
              <AlertPartial>{errors.email}</AlertPartial>
            )}
          </div>
        </div>
        <div className="row">
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="firstName" className="form-label">
                First Name
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>

            <input
              type="text"
              className="form-control"
              id="firstName"
              value={userInfo.firstName}
              onChange={onChangeInput}
            />
            {errors && errors.firstName && (
              <AlertPartial>{errors.firstName}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="lastName" className="form-label">
                Last Name
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="lastName"
              value={userInfo.lastName}
              onChange={onChangeInput}
            />
            {errors && errors.lastName && (
              <AlertPartial>{errors.lastName}</AlertPartial>
            )}
          </div>
        </div>

        <div className="row">
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="phone" className="form-label">
                Phone Number
              </label>
            </div>
            <input
              type="tel"
              pattern="[0-9]{10}"
              title="Review phone Number"
              className="form-control"
              id="phone"
              value={userInfo.phone}
              onChange={onChangeInput}
            />
            {errors && errors.phone && (
              <AlertPartial>{errors.phone}</AlertPartial>
            )}
          </div>
          {/* address */}

          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="country" className="form-label">
                country
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="country"
              value={userInfo.country}
              onChange={onChangeInput}
            />
            {errors && errors.country && (
              <AlertPartial>{errors.country}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="city" className="form-label">
                city
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="city"
              value={userInfo.city}
              onChange={onChangeInput}
            />
            {errors && errors.city && (
              <AlertPartial>{errors.city}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="street" className="form-label">
                street
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="street"
              value={userInfo.street}
              onChange={onChangeInput}
            />
            {errors && errors.street && (
              <AlertPartial>{errors.street}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="houseNumber" className="form-label">
                House Number
              </label>
            </div>
            <input
              type="number"
              className="form-control"
              id="houseNumber"
              value={userInfo.houseNumber}
              onChange={onChangeInput}
            />
            {errors && errors.houseNumber && (
              <AlertPartial>{errors.houseNumber}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="zip" className="form-label">
                zip
              </label>
            </div>
            <input
              type="number"
              className="form-control"
              id="zip"
              value={userInfo.zip}
              onChange={onChangeInput}
            />
            {errors && errors.zip && <AlertPartial>{errors.zip}</AlertPartial>}
          </div>
        </div>

        <div className="row">
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="isVip" className="form-label">
                VIP member
              </label>
            </div>
            {/* <input
              type="text"
              className="form-control"
              id="isVip"
              value={userInfo.isVip}
              onChange={onChangeInput}
            /> */}
            <select
              className="form-select"
              aria-label="Default select example"
              id="isVip"
              onChange={onChangeInput}
              value={userInfo.isVip}
            >
              <option defaultValue={"default"}>Select Filed</option>
              <option value={true}>True</option>
              <option value={false}>False</option>
            </select>
            {errors && errors.isVip && (
              <AlertPartial>{errors.isVip}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-6 mt-3 mb-6">
            <div className="d-flex  justify-content-center">
              <label htmlFor="userIMG" className="form-label">
                User IMG
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="userIMG"
              value={userInfo.userIMG}
              onChange={onChangeInput}
            />
            {errors && errors.userIMG && (
              <AlertPartial>{errors.userIMG}</AlertPartial>
            )}
          </div>
        </div>
        <BtnPartial btnColor={btnColorOption.secondary} onClick={getUserData}>
          Get Information
        </BtnPartial>
        <BtnPartial
          btnColor={btnColorOption.secondary}
          onClick={updateUserData}
        >
          Update
        </BtnPartial>
        <BtnPartial
          btnColor={btnColorOption.secondary}
          onClick={deleteUserByEmail}
        >
          Delete
        </BtnPartial>
        <BtnPartial btnColor={btnColorOption.secondary} onClick={unblockUser}>
          Unblock User
        </BtnPartial>
      </div>
    </form>
  );
};

export default UserManagementPage;
