import Joi from "joi";
import validate from "./validate";

const resetSchema = Joi.object({
    email: Joi.string()
    .min(10)
    .max(200)
    .email({ tlds: { allow: false } })
    .required()
    .trim()
    .messages({ "string.email": "Invalid Email" }),
  password: Joi.string()
    .regex(
      new RegExp(
        "^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*()+-_=]).{3,255}"
      )
    )
    .required().min(8)
    .messages({
      "string.pattern.base":
        "Password must contain an upper and lower case letter, number and a symbol",
    }),
    newPassword: Joi.string()
    .regex(
      new RegExp(
        "^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*()+-_=]).{3,255}"
      )
    )
    .required().min(8)
    .messages({
      "string.pattern.base":
        "Password must contain an upper and lower case letter, number and a symbol",
    }),
  phone: Joi.string().required()
    .min(10)
    .max(14)
    .messages({ "string.base": "phone Missing / Invalid filed" }),
})

const resetSchemaValidation = (userInput)=>{
    return validate(resetSchema, userInput)

}

export default resetSchemaValidation;