// imports
import PropTypes from "prop-types";
import { memo } from "react";

// deleration
const btnColorOption = {
  primary: "primary",
  secondary: "secondary",
  success: "success",
  danger: "danger",
  warning: "warning",
  info: "info",
  light: "light",
  dark: "dark",
};

const BtnPage = ({ btnColor, children, icon, onClick }) => {
  const defaultOption = btnColorOption.primary;


  return (
    <button
      className={` btn btn-outline-${btnColor ? btnColor : defaultOption}`} onClick={onClick} >
      {icon && <i className={` bi bi-${icon} listIcon`}></i>
     }

      {children}
    </button>
  );
};

BtnPage.propTypes = {
  btnColor: PropTypes.oneOf(Object.values(btnColorOption)),
  children: PropTypes.string,
  onClick: PropTypes.func,
};

BtnPage.defaultProp = {
  btnColor: btnColorOption.secondary,
};

export { btnColorOption };
export default memo(BtnPage);
