//   schema.validate(userInput, { abortEarly: false });

// export default validate;

const validate = (schema, userInput) => {
  const { error } = schema.validate(userInput, { abortEarly: false });
  if (error) {
    let errorObj = {};
    let newKey = ""
    for (let err of error.details) {
      if (!errorObj[err.context.key]) {
        newKey = err.context.key
        errorObj[newKey] = [];
      }
      errorObj[newKey] = [...errorObj[newKey], err.message, " "];
    }
    return errorObj;
  } else {
    return null;
  }
};

export default validate;
