import Joi from "joi";
import validate from "./validate";

const unblockUserSchema = Joi.object({
  adminEmail: Joi.string()
    .min(10)
    .max(200)
    .email({ tlds: { allow: false } })
    .required()
    .trim()
    .messages({ "string.email": "Invalid Email" }),
  email: Joi.string()
    .min(10)
    .max(200)
    .email({ tlds: { allow: false } })
    .required()
    .trim()
    .messages({ "string.email": "Invalid Email" }),
});

const unblockUserSchemaValidation = (userInput) => {
  return validate(unblockUserSchema, userInput);
};

export default unblockUserSchemaValidation;
