import { useState, Fragment } from "react";
import { toast } from "react-toastify";
import registerSchemaValidation from "../validation/registerValidation";
import AlertPartial from "../partials/AlertPartial";
import axios from "axios";

const RegisterAdminPage = () => {

  const [registerInputs, setRegisterInputs] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    phone: "",
    country: "",
    city: "",
    street: "",
    houseNumber: "",
    zip: "",
  });

  const [errors, setErrors] = useState(null);

  const onChangeInput = (event) => {
    let registerInputsCopy = JSON.parse(JSON.stringify(registerInputs));
    registerInputsCopy[event.target.id] = event.target.value;
    setRegisterInputs(registerInputsCopy);
  };

  const submitForm = async (event) => {
    event.preventDefault();
    try {
      const errors = registerSchemaValidation(registerInputs);
      if (errors) {
        setErrors(errors);
      } else {
        setErrors(null);
        await axios.post("/admin/registerAdmin", {
          firstName: registerInputs.firstName,
          lastName: registerInputs.lastName,
          email: registerInputs.email,
          password: registerInputs.password,
          phone: registerInputs.phone,
          isAdmin: true,
          address: {
            country: registerInputs.country,
            city: registerInputs.city,
            street: registerInputs.street,
            houseNumber: registerInputs.houseNumber,
            zip: registerInputs.zip,
          },
        });
        toast("Admin Created!");
      }
    } catch (error) {
      let errorArr = [];
      const { err } = error.response.data;
      if (err.details) {
        const { details } = err;
        for (let i = 0; i < details.length; i++) {
          errorArr = [...errorArr, details[i]];
          toast(details[i]);
        }
        toast(errorArr);
      } else {
        const { filed } = err;
        toast(filed[0]);
      }
    }
  };

  return (
    <Fragment>
   
      <form
        className={"container-fluid  p-5 pt-2"}
        onSubmit={submitForm}
        
      >
        <div className="row mt-2">
          <div className="form-group col-md-6 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="email" className="form-label mt-3">
                Email address
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>
            <input
              type="email"
              className="form-control"
              id="email"
              aria-describedby="emailHelp"
              value={registerInputs.email}
              onChange={onChangeInput}
            />
            {errors && errors.email && (
              <AlertPartial>{errors.email}</AlertPartial>
            )}
            <div id="emailHelp" className="form-text">
              We'll never share your email with anyone else.
            </div>
          </div>
          <div className="form-group col-md-6 mt-2">
            <div className="d-flex justify-content-center">
              <label htmlFor="password" className="form-label mt-3">
                Password
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>
            <input
              type="password"
              className="form-control"
              id="password"
              value={registerInputs.password}
              onChange={onChangeInput}
            />
            {errors && errors.password && (
              <AlertPartial>{errors.password}</AlertPartial>
            )}
          </div>
        </div>
        <div className="row">
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="firstName" className="form-label">
                First Name
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>

            <input
              type="text"
              className="form-control"
              id="firstName"
              value={registerInputs.firstName}
              onChange={onChangeInput}
            />
            {errors && errors.firstName && (
              <AlertPartial>{errors.firstName}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="lastName" className="form-label">
                Last Name
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="lastName"
              value={registerInputs.lastName}
              onChange={onChangeInput}
            />
            {errors && errors.lastName && (
              <AlertPartial>{errors.lastName}</AlertPartial>
            )}
          </div>
        </div>

        <div className="row">
          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="phone" className="form-label">
                Phone Number
              </label>
            </div>
            <input
              type="tel"
              pattern="[0-9]{10}"
              title="Review phone Number"
              className="form-control"
              id="phone"
              value={registerInputs.phone}
              onChange={onChangeInput}
            />
            {errors && errors.phone && (
              <AlertPartial>{errors.phone}</AlertPartial>
            )}
          </div>
          {/* address */}

          <div className="form-group col-md-6 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="country" className="form-label">
                country
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="country"
              value={registerInputs.country}
              onChange={onChangeInput}
            />
            {errors && errors.country && (
              <AlertPartial>{errors.country}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="city" className="form-label">
                city
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="city"
              value={registerInputs.city}
              onChange={onChangeInput}
            />
            {errors && errors.city && (
              <AlertPartial>{errors.city}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="street" className="form-label">
                street
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              id="street"
              value={registerInputs.street}
              onChange={onChangeInput}
            />
            {errors && errors.street && (
              <AlertPartial>{errors.street}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="houseNumber" className="form-label">
                House Number
              </label>
            </div>
            <input
              type="number"
              className="form-control"
              id="houseNumber"
              value={registerInputs.houseNumber}
              onChange={onChangeInput}
            />
            {errors && errors.houseNumber && (
              <AlertPartial>{errors.houseNumber}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-3 mt-3 mb-3">
            <div className="d-flex  justify-content-center">
              <label htmlFor="zip" className="form-label">
                zip
              </label>
            </div>
            <input
              type="number"
              className="form-control"
              id="zip"
              value={registerInputs.zip}
              onChange={onChangeInput}
            />
            {errors && errors.zip && <AlertPartial>{errors.zip}</AlertPartial>}
          </div>
        </div>
        <div className="row">
          <div className="col-md-4"></div>
          <div className="col-md-4 col-sm-12">
            <button
              type="submit"
              className="btn btn-outline-secondary mt-3 p-2 d-flex justify-content-center col-md-12 col-sm-12 "
            >
              Register
            </button>
          </div>
          <div className="col-md-4"></div>
        </div>
      </form>
    </Fragment>
  );
};

export default RegisterAdminPage;
