// imports
import PropTypes from "prop-types";
import { memo } from "react";

// deleration
const btnColorOption = {
  primary: "primary",
  secondary: "secondary",
  success: "success",
  danger: "danger",
  warning: "warning",
  info: "info",
  light: "light",
  dark: "dark",
};

const BtnSearch = ({ btnColor, children, onClick }) => {
  const defaultOption = btnColorOption.primary;



  return (
    <button
    id="listBtn"
      className={` btn btn-outline-${btnColor ? btnColor : defaultOption}`} onClick={onClick} >
      {children}
    </button>
  );
};

BtnSearch.propTypes = {
  btnColor: PropTypes.oneOf(Object.values(btnColorOption)),
  children: PropTypes.string,
  onClick: PropTypes.func,
};

BtnSearch.defaultProp = {
  btnColor: btnColorOption.secondary,
};

export { btnColorOption };
export default memo(BtnSearch);
