import { Route, Routes } from "react-router-dom";
import Page404 from "../pages/Page404";
import CategoryPage from "../pages/CategoryPage";
// import EditPage from "../pages/EditCoursePage";
// import EditUserPage from "../pages/EditUserPage";
import EditCoursePage from "../pages/EditCoursePage";
import UserManagementPage from "../pages/UserManagementPage";
import ResetPasswordPage from "../pages/RestPasswordPage";
import UnblockPage from "../pages/UnblockPage";
import AddCoursePage from "../pages/AddCoursePage";
import ProfilePage from "../pages/ProfilePage";
import ContactUsPage from "../pages/ContactUsPage";
import AboutUsPage from "../pages/AboutUsPage";
import LoginPage from "../pages/LoginPage";
import RegisterAdminPage from "../pages/RegisterAdminPage";
import RegisterPage from "../pages/RegisterPage";
import HomePage from "../pages/HomePage";
import ROUTES from "./routes";


import NoPermissionPage from "../pages/NoPermissionPage";

import WishListPage from "../pages/WishListPage";
import VipPage from "../pages/VipPage";

import ProtectedUsers from "./protectedUsers";
import ProtectedAdmin from "./protectedAdmin";

const Router = () => {
  return (
    <Routes>
      {/* open to all */}
      <Route path={ROUTES.HOME} element={<HomePage />} />
      <Route path={ROUTES.ABOUT} element={<AboutUsPage />} />
      <Route path={ROUTES.CONTACT} element={<ContactUsPage />} />
      <Route path={ROUTES.REGISTER} element={<RegisterPage />} />
      <Route path={ROUTES.LOGIN} element={<LoginPage />} />
      <Route path={ROUTES.RESETPASS} element={<ResetPasswordPage />} />
      <Route path={ROUTES.SHOWBYCATEGORY} element={<CategoryPage />} />
      <Route path={ROUTES.NOPermission} element={<NoPermissionPage />} />
      <Route path={ROUTES.WISHLIST} element={<WishListPage />} />
      <Route path={ROUTES.VIP} element={<VipPage/>} />


      {/* open to users */}
      <Route
        path={ROUTES.PROFILE}
        element={<ProtectedUsers element={<ProfilePage />} />}
      />

      {/* open to admin */}

      <Route
        path={ROUTES.ADDCOURSE}
        element={<ProtectedAdmin element={<AddCoursePage />} />}
      />
      <Route
        path={ROUTES.EDITCOURSE}
        element={<ProtectedAdmin element={<EditCoursePage />} />}
      />
      <Route
        path={ROUTES.ADDADMIN}
        element={<ProtectedAdmin element={<RegisterAdminPage />} />}
      />

      <Route
        path={ROUTES.EDITUSER}
        element={<ProtectedAdmin element={<UserManagementPage />} />}
      />
      <Route
        path={ROUTES.UNBLOCK}
        element={<ProtectedAdmin element={<UnblockPage />} />}
      />
  

      

      {/* DNU 
            
             <Route path="/add" element={<AddCoursePage/>}  /> 
             <Route path="/unblock" element={<UnblockPage/>}  /> 
             <Route path="/editUser" element={<EditUserPage/>}  /> 
             <Route path="/edit/:prodID" element={<EditPage/>}  /> 
             <Route path="/showByCategory/:category" element={<CategoryPage/>}  /> */}

      {/* to create */}
      {/* <Route path="/order" element={<OrderPage/>}  /> */}

      {/* 404 page */}
      <Route path="*" element={<Page404 />} />
    </Routes>
  );
};

export default Router;
