import Joi from "joi";
import validate from "./validate";

const courseSchema = Joi.object({
  courseName: Joi.string().min(3).max(30).trim().allow(null, "").pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  courseImg: Joi.any().allow(null, ""),

  category: Joi.string().min(3).max(30).trim().allow(null, "").pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),


  lecturer: Joi.string().min(3).max(30).trim().allow(null, "").pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),


  description: Joi.string().min(3).max(300).trim().allow(null, "")
  .messages({ "string.base": "description Missing / Invalid filed" }),

  coursePrice: Joi.number().allow(null, "").integer().min(15)
  .messages({ "string.base": "price Missing / Invalid filed" }),

  privetPrice: Joi.number().allow(null, "").integer().min(15)
  .messages({ "string.base": "price Missing / Invalid filed" }),

  totalHours: Joi.number().allow(null, "").integer().min(15)
  .messages({ "string.base": "PrivetSession Missing / Invalid filed" }),

  copyStockAmount: Joi.number().allow(null, "").integer().min(15)
  .messages({ "string.base": "printCopy Missing / Invalid filed" }),
  
  copyPrice: Joi.number().allow(null, "").integer().min(15)
  .messages({ "string.base": "printCopy Missing / Invalid filed" }),
  });

const courseSchemaValidation = (userInput) => {
  return validate(courseSchema, userInput);
};

export default courseSchemaValidation;
