// components
import NavbarComponent from "./components/Navbar/NavbarComponent";
import FooterCop from "./partials/FooterCop";
import useLogin from "./hooks/useLogin";
import Router from "./routes/router";
import { Fragment, useEffect } from "react";
import "./myStyle.css";
import { Flip, ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useSelector } from "react-redux";


let color = {
  bgL: "bgL",
  bgD: "bgD",
  textL: "textL",
  textD: "textD",
};

const App = () => {
  const theme = useSelector((state) => state.myDisplay.theme);
  const loginFunc = useLogin();

  useEffect(() => {
    loginFunc();
  },[]);



  return (
    <Fragment>
      <ToastContainer
        position={toast.POSITION.TOP_CENTER}
        transition={Flip}
        autoClose={3000}
        hideProgressBar={true}
      />
      <header>
      <div className="container-fluid">
          <div className="row">
            <div className={theme ? `p-0 col-12 col-lg-11 mx-auto col-md-10  ${color.bgL} ${color.textD}` :
          `p-0 col-12 col-lg-11 mx-auto col-md-10 ${color.bgD} ${color.textL}`}>
        <NavbarComponent />
        </div>
        </div>
        </div>
      </header>

      <main>
        <div className="container-fluid">
          <div className="row">
            <div className={theme ? `px-0 col-12 col-lg-11 mx-auto col-md-10  ${color.bgL} ${color.textD}` :
          `col-12 col-lg-11 mx-auto col-md-10 ${color.bgD} ${color.textL}`}>
              <Router />
            </div>
          </div>
        </div>
      </main>

      <footer>
      <div className="container-fluid">
          <div className="row">
            <div className={theme ? `col-12 col-lg-11 mx-auto col-md-10  ${color.bgL} ${color.textD}` :
          `col-12 col-lg-11 mx-auto col-md-10 ${color.bgD} ${color.textL}`}>
        <FooterCop />
        </div>
        </div>
        </div>
      </footer>
    </Fragment>
  );
};

export default App;
