import PropTypes from "prop-types";
import BtnPartial from "../partials/BtnPartial";
import { Fragment, useState } from "react";
import orderSchemaValidation from "../validation/orderValidation";
import { toast } from "react-toastify";
import { btnColorOption } from "../partials/BtnPartial";
import AlertPartial from "../partials/AlertPartial";
import { useSelector } from "react-redux";

const CourseCardComponent = ({
  courseName,
  lecturer,
  category,
  description,
  price,
  onAddToWishList,
  onOrder,
  onDelete,
  onEdit,
  id,
  isAdmin,
  courseImg,
  lecturerEmail,
  inList,
  onRemoveFromList
}) => {
  const userInfo1 = useSelector((state) => state.myAuth.userInfo);
  const { isVip } = userInfo1;
  const [errors, setErrors] = useState(null);
  const [orderInput, setOrderInput] = useState({
    copy: 0,
    privet: 0,
  });

  const handleOrderInputChange = (event) => {
    let orderInputCopy = JSON.parse(JSON.stringify(orderInput));
    orderInputCopy[event.target.name] = event.target.value;
    setOrderInput(orderInputCopy);
  };

  const handleAddToWishClick = () => {
    onAddToWishList(id);
  };

  const handleOrderClick = () => {
    setErrors({});
    if (orderInput.copy < 0 || orderInput.privet < 0) {
      toast(" Quantity Invalid ");
    } else {
      let data = {
        copiesNeeded: orderInput.copy,
        privetLesson: orderInput.privet,
        courseID: id,
      };
      const validationData = orderSchemaValidation(data);
      if (validationData) {
        setErrors(validationData);
      } else {
        onOrder(id, orderInput.copy, orderInput.privet);
      }
    }
  };

  const handleRemoveFromWishList = ()=>{
    onRemoveFromList(id)
  }

  const handleEditClick = () => {
    onEdit(id);
  };

  const handleDeleteClick = () => {
    onDelete(id);
  };

  // const handleEmailClick = () => {};

  return (
    <div className={`card-deck pt-2`}>
      <div className="card-deck">
        <div className="">
          <div className="card">
            <img
              src={`/uploads/${courseImg}`}
              className="cardIMG rounded card-img-top img-responsive cardIMG object-fit-cover  "
              alt="img"
            />
            <div className="card-body text-dark">
              <h5 className="card-title cc">{courseName}</h5>
              <h6 className="card-subtitle md-2 text-muted cc">{category}</h6>
              <p className="card-text">
                <small className="text-body-secondary cc">By {lecturer}</small>
              </p>
              <div
                className="overflow-auto"
                style={{ height: "35px", overflowY: "scroll" }}
              >
                <p className="card-text lc"> {description}</p>
              </div>
            </div>
            <ul className="list-group list-group-flush">
              <li className="list-group-item ">
                Online Price: {price.coursePrice}$
              </li>
              <li className="list-group-item">
                Privet Lesson: {price.privetPrice}$
              </li>
              <li className="list-group-item">
                Notebook here: {price.copyPrice}$
              </li>
              <li className="list-group-item">
                {" "}
                <label className="form-label copy">
                  Copy number:
                  <input
                    type="number"
                    className="form-control"
                    name="copy"
                    value={orderInput.copy}
                    onChange={handleOrderInputChange}
                  />
                  {errors && errors.copiesNeeded && (
                    <AlertPartial>{errors.copiesNeeded}</AlertPartial>
                  )}
                </label>
              </li>
              <li className="list-group-item">
                {" "}
                <label className="form-label privet">
                  Lesson number:
                  <input
                    type="number"
                    className="form-control"
                    name="privet"
                    value={orderInput.privet}
                    onChange={handleOrderInputChange}
                  />
                  {errors && errors.privetLesson && (
                    <AlertPartial>{errors.privetLesson}</AlertPartial>
                  )}
                </label>
              </li>
            </ul>

            <div className="card-footer">
              {!isAdmin ? (
                <Fragment>
                  {!inList ? (
                       <BtnPartial
                       btnColor={btnColorOption.secondary}
                       children={"Add To Wish"}
                       onClick={handleAddToWishClick}
                     />
                  ): (
                    <BtnPartial
                    btnColor={btnColorOption.secondary}
                    children={"Remove From List"}
                    onClick={handleRemoveFromWishList}
                  />
                  )
                  // <BtnPartial
                  //   btnColor={btnColorOption.secondary}
                  //   children={"Add To Wish"}
                  //   onClick={handleAddToWishClick}
                  // />
                }
                  <BtnPartial
                    btnColor={btnColorOption.secondary}
                    children={"Order Now"}
                    onClick={handleOrderClick}
                  />
                  {isVip ? (
                    <form
                      action={`mailto:${lecturerEmail}`}
                      method="post"
                      encType="text/plain"
                    >
                      <BtnPartial
                        btnColor={btnColorOption.secondary}
                        children={`Email ${lecturer}`}
                        // onClick={handleEmailClick}
                      />
                    </form>
                  ) : (
                    " "
                  )}
                </Fragment>
              ) : (
                <Fragment>
                  <BtnPartial
                    btnColor={btnColorOption.secondary}
                    children={"Edit Now"}
                    onClick={handleEditClick}
                  />
                  <BtnPartial
                    btnColor={btnColorOption.secondary}
                    children={"Delete Now"}
                    onClick={handleDeleteClick}
                  />
                </Fragment>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

CourseCardComponent.propTypes = {
  courseName: PropTypes.string,
  lecturer: PropTypes.string,
  category: PropTypes.string,
  description: PropTypes.string,
  price: PropTypes.shape({
    coursePrice: PropTypes.number,
    privetPrice: PropTypes.number,
    copyPrice: PropTypes.number,
  }),

  onAddToWishList: PropTypes.func,
  onOrder: PropTypes.func,
  id: PropTypes.string,
  onDelete: PropTypes.func,
  onEdit: PropTypes.func,
  isAdmin: PropTypes.bool,
  img: PropTypes.string,
  inList: PropTypes.bool,
  onRemoveFromList: PropTypes.func
};

CourseCardComponent.defaultProp = {
  isAdmin: false,
  inList: false,
};

export default CourseCardComponent;
