import PropTypes from "prop-types";
import BtnPartial from "../partials/BtnPartial";
import { Fragment, useState } from "react";
import orderSchemaValidation from "../validation/orderValidation";
import { toast } from "react-toastify";
import { btnColorOption } from "../partials/BtnPartial";


const ListViewComponent = ({
  courseName,
  lecturer,
  description,
  price,
  onAddToWishList,
  onOrder,
  onDelete,
  onEdit,
  id,
  isAdmin,
  courseImg,
}) => {
  

  const [privet, setPrivet] = useState(0);
  const [copy, setCopy] = useState(0);
  const [errors, setErrors] = useState(null);

  const handleOrderInputChange = () => {
    
  };

  const plusPrivet = () => {
    setPrivet((prevState) => (prevState = +prevState + 1));
  };

  const minPrivet = () => {
    setPrivet((prevState) => (prevState = +prevState - 1));
  };

  const plusCopy = () => {
    setCopy((prevState) => (prevState = +prevState + 1));
  };

  const minCopy = () => {
    setCopy((prevState) => (prevState = +prevState - 1));
  };

  const handleAddToWishClick = () => {
    onAddToWishList(id);
  };

  const handleOrder = () => {
    setErrors({});
    if (copy < 0 || privet < 0) {
      toast(" Quantity Invalid ");
    } else {
      let data = {
        copiesNeeded: copy,
        privetLesson: privet,
        courseID: id,
      };
      const validationData = orderSchemaValidation(data);
      if (validationData) {
        setErrors(validationData);
      } else {
        onOrder(id, data.copiesNeeded, data.privetLesson);
      }
    }
  };

  const handleEdit = () => {
    onEdit(id);
  };

  const handleDelete = () => {
    onDelete(id);
  };

  return (
    <div className="mainCo row mt-2">
      <div className=" col-sm-1">
        <img
          src={`/uploads/${courseImg}`}
          className=" listImg rounded-circle  card-img-top img-responsive cardIMG "
          alt="Card image cap"
        />
      </div>

      <div className=" col-sm-1 col-md-1 col-lg-1">
        <div className="uc col-sm pt-2 ">{courseName}</div>
        <div className=" col-sm pb-2">
          <p className="cc primaryInfo">By {lecturer}</p>
          <p className="secondaryInfo">{price.coursePrice}$</p>
        </div>
      </div>

      <div className="cc col-sm-1 col-md-1 col-lg-2 p-2">
        <p>{description}</p>
      </div>

      <div className="col-sm-1 col-md-2 col-lg-3 d-flex flex-row">
        <div className=" d-flex">
          <div className="p-2">
            <p className="cc primaryInfo">Privet Lesson</p>
            <p className="secondaryInfo"> {price.privetPrice}$</p>
          </div>
          <div className="p-2">
            <i className="bi bi-plus listIcon" onClick={plusPrivet}></i>
          </div>
          <div className="p-2">
            <input
              type="number"
              className="input form-control"
              aria-label="Small"
              aria-describedby="inputGroup-sizing-sm"
              id="privet"
              value={privet}
              onChange={handleOrderInputChange}
            />
          </div>
          <div className="p-2">
            <i className="bi bi-dash listIcon" onClick={minPrivet}></i>
          </div>
        </div>
      </div>

      <div className="col-sm-1 col-md-2 col-lg-3 d-flex flex-row">
        <div className=" d-flex">
          <div className="p-2">
            <p className="cc primaryInfo">NoteBook</p>
            <p className="secondaryInfo"> {price.copyPrice}$</p>
          </div>
          <div className="p-2">
            <i className="bi bi-plus listIcon" onClick={plusCopy}></i>
          </div>
          <div className="p-2">
          <input
              type="number"
              className="input form-control"
              aria-label="Small"
              aria-describedby="inputGroup-sizing-sm"
              id="copy"
              value={copy}
              onChange={handleOrderInputChange}
            />
          </div>
          <div className="p-2">
            <i className="bi bi-dash listIcon" onClick={minCopy}></i>
          </div>
        </div>
      </div>
      <div className="col-sm-6 col-md-3 col-lg-2 d-flex justify-content-center">
        {!isAdmin ? (
          <div className="d-sm-content-center">
            <div className="col-sm d-flex mt-1 mb-1">
              <BtnPartial
                btnColor={btnColorOption.secondary}
                children={"Add To Wish"}
                onClick={handleAddToWishClick}
              />

              <BtnPartial
                btnColor={btnColorOption.secondary}
                children={"Order Now"}
                onClick={handleOrder}
              />
            </div>
          </div>
        ) : (
          <Fragment>
            <div className="col-sm d-flex mt-1 mb-1">
              <BtnPartial
                btnColor={btnColorOption.secondary}
                children={"Edit Now"}
                onClick={handleEdit}
              />
              <BtnPartial
                btnColor={btnColorOption.secondary}
                children={"Delete Now"}
                onClick={handleDelete}
              />
            </div>
          </Fragment>
        )}
      </div>
    </div>
 
  );
};

ListViewComponent.propTypes = {
  courseName: PropTypes.string,
  lecturer: PropTypes.string,
  category: PropTypes.string,
  description: PropTypes.string,
  price: PropTypes.shape({
    coursePrice: PropTypes.number,
    privetPrice: PropTypes.number,
  }),
  onAddToWishList: PropTypes.func,
  onOrder: PropTypes.func,
  id: PropTypes.string,
  onDelete: PropTypes.func,
  onEdit: PropTypes.func,
  isAdmin: PropTypes.bool,
  img: PropTypes.string,
};

ListViewComponent.defaultProp = {
  isAdmin: false,
};

export default ListViewComponent;
