import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import editCourseSchemaValidation from "../validation/editCourseValidation";
import AlertPartial from "../partials/AlertPartial";
import BtnPartial from "../partials/BtnPartial";
import { btnColorOption } from "../partials/BtnPartial";
import axios from "axios";






const EditCoursePage = () => {
  let { prodID } = useParams();

  const [editInputs, setEditInputs] = useState({
    productID: prodID,
    filedToUpdate: "",
    dataUpdated: "",
  });

  const [errors, setErrors] = useState({});

  const navigate = useNavigate()

  const handleInputChange = (event) => {
    let editInputsCopy = JSON.parse(JSON.stringify(editInputs));
    editInputsCopy[event.target.id] = event.target.value;
    setEditInputs(editInputsCopy);
  };

 


   
 

   
  



  const handleUpdateClick = async (event) => {
    event.preventDefault();
    try {
      const validateUserInput = editCourseSchemaValidation(editInputs);
      if (validateUserInput) {
        let copyErrorObg = {};
        setErrors(copyErrorObg);
        for (const [key, value] of Object.entries(validateUserInput)) {
   
          value.map((msg)=>( copyErrorObg[key] = msg.replace("",'')))
        }
        setErrors(copyErrorObg);
        copyErrorObg = errors
      } else {
       
        const { data } = await axios.patch("admin/editCourse", {
          productID: editInputs.productID,
          filedToUpdate: editInputs.filedToUpdate,
          dataUpdated: editInputs.dataUpdated,
        });
        toast(data.msg)
      }
    } catch (error) {

      const {err} = error.response.data
 
      let msg = err.filed[0]
  
      toast(msg)

    }
  };

  const homePageNavigation = ()=>{
    navigate("/");
  }

  return (
    <div className= {`container-fluid pt-2 pb-4`}>
      <form onSubmit={handleUpdateClick}>
        <div className="form-group">
          <label htmlFor="productID">productID</label>
          <fieldset disabled>

          <input
            type="string"
            className="form-control"
            id="productID"
            value={editInputs.productID}
            onChange={handleInputChange}
          />
           </fieldset>
        </div>
        <br />
        <div className="form-group">
          <select
            className="form-select"
            aria-label="Default select example"
            id="filedToUpdate"
            onChange={handleInputChange}
          >
            <option defaultValue={"hhh"}>Select Filed</option>
            <option value={"courseName"}>Course Name</option>
            <option value={"category"}>Category</option>
            <option value={"lecturer"}>Lecturer</option>
            <option value={"description"}>Description</option>
            <option value={"price.coursePrice"}>Course Price</option>
            <option value={"price.privetPrice"}>Privet Lesson Price</option>
            <option value={"price.copyPrice"}>Notebook Price</option>
            <option value={"printCopy.copyStockAmount"}>
              Booklet Copy Quantity
            </option>
            
          </select>
          {errors && errors.filedToUpdate && (
            <AlertPartial>{errors.filedToUpdate}</AlertPartial>
          )}
        </div>
        <br />
        <div className="form-group">
          <label htmlFor="dataUpdated">dataUpdated</label>
          <input
            className="form-control"
            id="dataUpdated"
            placeholder="dataUpdated"
            value={editInputs.dataUpdated}
            onChange={handleInputChange}
          />
          {errors && errors.dataUpdated && (
            <AlertPartial>{errors.dataUpdated}</AlertPartial>
          )}
        </div>
        <br />
        <BtnPartial btnColor={btnColorOption.secondary}>Update</BtnPartial>
        <BtnPartial btnColor={btnColorOption.secondary} onClick={homePageNavigation}>Back to Home</BtnPartial>
      </form>
    </div>
  );
};

export default EditCoursePage;
