import PropTypes from "prop-types";
import { useSelector } from "react-redux";
import { Navigate } from "react-router-dom";
import ROUTES from "./routes";

const ProtectedAdmin = ({ element }) => {
  let userLogged = useSelector((state) => state.myAuth.isLogged);
  let isAdmin = useSelector((state) => state.myAuth.userInfo.isAdmin);


  if (userLogged && isAdmin) {
    return element;
  } else {
    // add hucking page
    return <Navigate to={ROUTES.NOPermission} replace />;
  }
};

ProtectedAdmin.propTypes = {
  element:PropTypes.any
}

export default ProtectedAdmin;
