import { Fragment, useState } from "react";
import { toast } from "react-toastify";
import axios from "axios";
import { btnColorOption } from "../partials/BtnPartial";
import BtnPartial from "../partials/BtnPartial";
import AlertPartial from "../partials/AlertPartial";
import courseSchemaValidation from "../validation/addCourseValdation";

const AddCoursePage = () => {
  // const userInfoTOken = useSelector((state) => state.myAuth.userInfo);
  const [errors, setErrors] = useState(null);
  const [courseFiled, setCourseFiled] = useState({
    courseName: "",
    category: "",
    lecturer: "",
    description: "",
    coursePrice: 30,
    privetPrice: 30,
    totalHours: 30,
    copyStockAmount: 100,
    copyPrice: 15,
  });

  const clearData = {
    courseName: "",
    category: "",
    lecturer: "",
    description: "",
    coursePrice: 30,
    privetPrice: 30,
    totalHours: 30,
    copyStockAmount: 100,
    copyPrice: 15,
  }

  const [avatarFile, setAvatar] = useState(null);

  const handleFiledChange = (event) => {
    let inputsCopy = JSON.parse(JSON.stringify(courseFiled));
    inputsCopy[event.target.id] = event.target.value;
    setCourseFiled(inputsCopy);
  };

  const handleAvatarChange = (event) => {
    setAvatar(event.target.files[0]);
  };

  const defaultPricing = (filed) => {
    if (filed) {
      return filed;
    } else {
      return 30;
    }
  };

  const sendForm = async (event) => {
    event.preventDefault();
    setErrors({});
    const validatedInputsErrors = courseSchemaValidation(courseFiled);
    if (validatedInputsErrors) {
      setErrors(validatedInputsErrors);
    } else {
      setErrors(validatedInputsErrors);
      if (avatarFile) {
        let formData = new FormData();
        formData.append("courseName", courseFiled.courseName);
        formData.append("category", courseFiled.category);
        formData.append("lecturer", courseFiled.lecturer);
        formData.append("description", courseFiled.description);
        formData.append("totalHours", defaultPricing(courseFiled.totalHours));
        formData.append("courseImg", avatarFile, avatarFile.name);
        formData.append("coursePrice", defaultPricing(courseFiled.coursePrice));
        formData.append("privetPrice", defaultPricing(courseFiled.privetPrice));
        formData.append("copyPrice", defaultPricing(courseFiled.copyPrice));
        formData.append("copyStockAmount", courseFiled.copyStockAmount);

        try {
          const { data } = await axios.post("/admin/addCourseImg", formData);
          toast(data.msg);
        } catch (error) {
          const {err} = error.data
          toast(err)
         
        }
      } else {
        try {
          const { data } = await axios.post("/admin/addCourse", {
            courseName: courseFiled.courseName,
            category: courseFiled.category,
            lecturer: courseFiled.lecturer,
            description: courseFiled.description,
            price: {
              coursePrice: defaultPricing(courseFiled.coursePrice),
              privetPrice: defaultPricing(courseFiled.privetPrice),
              copyPrice: defaultPricing(courseFiled.copyPrice),
            },
            totalHours: courseFiled.totalHours,
            printCopy: {
              isPrintAvailable: courseFiled.isPrintAvailable,
              copyStockAmount: parseInt(courseFiled.copyStockAmount),
            },
            courseImg: courseFiled.courseImg,
          });
          toast(data.msg);
      
          setCourseFiled(clearData)
        } catch (error) {
          toast("error")
        }
      }
    }
  };

  return (
    <Fragment>
      <form className={"container-fluid  p-5 pt-2"}>
        {/*  */}

        <div className="row mt-2">
          <div className="form-group col-md-6 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="email" className="form-label mt-3">
                Course Name
              </label>
              <span>
                <i className="star bi bi-asterisk"></i>
              </span>
            </div>
            <input 
              type="text"
              className="form-control"
              name="courseName"
              id="courseName"
              value={courseFiled.courseName}
              onChange={handleFiledChange}
            />
            {errors && errors.courseName && (
              <AlertPartial>{errors.courseName}</AlertPartial>
            )}
          </div>
          <div className="form-group col-md-6 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="category" className="form-label mt-3">
                Category
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              name="category"
              id="category"
              value={courseFiled.category}
              onChange={handleFiledChange}
            />
            {errors && errors.category && (
              <AlertPartial>{errors.category}</AlertPartial>
            )}
          </div>
        </div>
        <div className="row">
          <div className="form-group col-md-6 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="lecturer" className="form-label mt-3">
                Lecturer
              </label>
              <span></span>
            </div>
            <input
              type="text"
              className="form-control"
              name="lecturer"
              id="lecturer"
              value={courseFiled.lecturer}
              onChange={handleFiledChange}
            />
            {errors && errors.lecturer && (
              <AlertPartial>{errors.lecturer}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-6 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="description" className="form-label mt-3">
                Description
              </label>
            </div>
            <input
              type="text"
              className="form-control"
              name="description"
              id="description"
              value={courseFiled.description}
              onChange={handleFiledChange}
            />
            {errors && errors.description && (
              <AlertPartial>{errors.description}</AlertPartial>
            )}
          </div>
        </div>

        {/*  */}
        <div className="row mt-2">
          <div className="form-group col-md-12 mt-2">
            <label htmlFor="exampleInputEmail1" className="my-3" >Course Image</label>
            <input
              type="file"
              className="form-control"
              name="courseImg"
              id="courseImg"
              value={courseFiled.test}
              onChange={handleAvatarChange}
            />
          </div>

          {/*  */}

          <div className="row">
            <div className="form-group col-md-6 mt-2">
              <div className="d-flex  justify-content-center">
                <label htmlFor="lecturer" className="form-label mt-3">
                  Total Hours
                </label>
                <span></span>
              </div>
              <input
                type="number"
                className="form-control"
                name="totalHours"
                id="totalHours"
                pattern="[0-9]{5}"
                title="Only Numbers Allowed"
                value={courseFiled.totalHours}
                onChange={handleFiledChange}
              />
              {errors && errors.totalHours && (
                <AlertPartial>{errors.totalHours}</AlertPartial>
              )}
            </div>

            <div className="form-group col-md-6 mt-2">
              <div className="d-flex  justify-content-center">
                <label htmlFor="description" className="form-label mt-3">
                  Copies Amount
                </label>
              </div>
              <input
                type="number"
                className="form-control"
                name="copyStockAmount"
                id="copyStockAmount"
                pattern="[0-9]{5}"
                title="Only Numbers Allowed"
                value={courseFiled.copyStockAmount}
                onChange={handleFiledChange}
              />
              {errors && errors.copyStockAmount && (
                <AlertPartial>{errors.copyStockAmount}</AlertPartial>
              )}
            </div>
          </div>

    
        </div>
 
        {/* <h3>Pricing</h3> */}

        <div className="row">
          <div className="form-group col-md-4 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="exampleInputEmail1" className="m-2">Online Course</label>
            </div>
            <input
              type="number"
              className="form-control"
              name="coursePrice"
              id="coursePrice"
              value={courseFiled.coursePrice}
              onChange={handleFiledChange}
            />
            {errors && errors.coursePrice && (
              <AlertPartial>{errors.coursePrice}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-4 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="exampleInputEmail1" className="m-2">Privet Lesson</label>
            </div>
            <input
              type="number"
              className="form-control"
              name="privetPrice"
              id="privetPrice"
              value={courseFiled.privetPrice}
              onChange={handleFiledChange}
            />
            {errors && errors.privetPrice && (
              <AlertPartial>{errors.privetPrice}</AlertPartial>
            )}
          </div>

          <div className="form-group col-md-4 mt-2">
            <div className="d-flex  justify-content-center">
              <label htmlFor="exampleInputEmail1" className="m-2">Notebook</label>
            </div>
            <input
              type="number"
              className="form-control"
              name="copyPrice"
              id="copyPrice"
              value={courseFiled.copyPrice}
              onChange={handleFiledChange}
            />
            {errors && errors.copyPrice && (
              <AlertPartial>{errors.copyPrice}</AlertPartial>
            )}
          </div>
        </div>

        <div className="row mt-5">
          <div className="col-md-4 col-sm-12"></div>
          <div className="col-md-4 col-sm-12 d-flex justify-content-center">
            <BtnPartial onClick={sendForm} btnColor={btnColorOption.secondary}>
              {"Add Course"}
            </BtnPartial>
          </div>
        </div>

        {
          // <BtnPartial onClick={sendForm} btnColor={btnColorOption.secondary}>
          //   {"Add Course"}
          // </BtnPartial>
        }
      </form>
    </Fragment>
  );
};

export default AddCoursePage;
