import Joi from "joi";
import validate from "./validate";

const loginSchema = Joi.object({
    email: Joi.string()
    .min(10)
    .max(200)
    .email({ tlds: { allow: false } })
    .required()
    .trim()
    .messages({ "string.email": "Invalid Email" }),
  password: Joi.string()
    .regex(
      new RegExp(
        "^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*()+-_=]).{3,255}"
      )
    )
    .required()
    .messages({
      "string.pattern.base":
      "Password must contain upper+lower case letter, number and a symbol",
    }),
})


const loginSchemaValidation = (userInput)=>{
    return validate(loginSchema, userInput)

}

export default loginSchemaValidation;