import Joi from "joi";
import validate from "./validate";

const registerSchema = Joi.object({
  firstName: Joi.string().min(3).max(200).trim().required().pattern(new RegExp('^[a-zA-Z]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  lastName: Joi.string().min(3).max(200).allow(null, "").pattern(new RegExp('^[a-zA-Z]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  email: Joi.string().email({ tlds: { allow: false } }).min(10).max(200).trim().required()
  .messages({  "string.pattern.base": "Invalid Email" }),

  password: Joi.string().required().min(8).regex(new RegExp("^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*()+-_=]).{3,255}"))
  .messages({"string.pattern.base":"Password must contain an upper and lower case letter, number and a symbol"}),

  phone: Joi.string().allow(null, "").min(8).max(20).pattern(new RegExp('^[0-9]{8,15}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  country: Joi.string().allow(null, "").min(3).max(200).pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  city: Joi.string().min(3).max(200).allow(null, "").pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  street: Joi.string().min(3).max(200).allow(null, "").pattern(new RegExp('^[a-zA-Z ]{3,30}$'))
  .messages({  "string.pattern.base": " Filed Missing / Invalid " }),

  houseNumber: Joi.string().min(1).max(200).trim().allow(null, "")
  .messages({ "string.base": " houseNumber Missing / Invalid filed" }),

  zip: Joi.string().min(3).max(200).trim().allow(null, "")
  .messages({ "string.base": " zip Missing / Invalid filed" }),

});

const registerSchemaValidation = (userInput) => {
  return validate(registerSchema, userInput);
};

export default registerSchemaValidation;
