import { Fragment, useState } from "react";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";

import BtnPartial from "../partials/BtnPartial";
import { btnColorOption } from "../partials/BtnPartial";
import loginSchemaValidation from "../validation/loginValidation";
import AlertPartial from "../partials/AlertPartial";
import useLogin from "../hooks/useLogin";
import ROUTES from "../routes/routes";

const LoginPage = () => {
  const [loginInput, setLoginInput] = useState({
    email: "",
    password: "",
  });

  const loginFunction = useLogin();

  const [errors, setErrors] = useState(null);

  const navigate = useNavigate();

  const handleInputChange = (event) => {
    let loginInputCopy = JSON.parse(JSON.stringify(loginInput));
    loginInputCopy[event.target.id] = event.target.value;
    setLoginInput(loginInputCopy);
    setErrors({});
  };


  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      setErrors({});
      const validatedInputsErrors = loginSchemaValidation(loginInput);
      if (validatedInputsErrors) {
   
        setErrors(validatedInputsErrors);
      } else {
     
        const { data } = await axios.post("/login", {
          email: loginInput.email,
          password: loginInput.password,
        });
       
        localStorage.setItem("token", data.userToken);
        loginFunction();
        navigate(ROUTES.HOME);
      }
    } catch (error) {
      const { err } = error.response.data;
    
      let msg = err.filed[0];
 
      toast(msg);
    }
  };

  const resetPasswordClick = (event) => {
    event.preventDefault();
  
    navigate(ROUTES.RESETPASS);
  };


  return (
    <Fragment>
      <form className={` p-5 pt-2`}>
        <div className="row mt-2 d-flex justify-content-center">
          <div className="form-group col-md-6 mt-2 ">
            <div className="mb-3 p-3">
              <label htmlFor="email" className="form-label">
                Email address
              </label>
              <input
                type="email"
                className="form-control"
                id="email"
                aria-describedby="emailHelp"
                value={loginInput.email}
                onChange={handleInputChange}
                autoComplete="on"
              />
              {errors && errors.email && (
                <AlertPartial>{errors.email}</AlertPartial>
              )}
              {/* <ErrorValidationComponent errorsArray={errors.emailInput} /> */}
              <div id="emailHelp" className="form-text">
                We'll never share your email with anyone else.
              </div>
            </div>
            <div className="mb-3 p-3 pt-2">
              <label htmlFor="password" className="form-label">
                Password
              </label>
              <input
                type="password"
                className="form-control"
                id="password"
                value={loginInput.password}
                onChange={handleInputChange}
                autoComplete="on"
              />
              {errors && errors.password && (
                <AlertPartial>{errors.password}</AlertPartial>
              )}
              {/* <ErrorValidationComponent errorsArray={errors.passwordInput} /> */}
            </div>

            <div className="mb-4 p-3 pt-2 d-flex justify-content-center">
              <BtnPartial
                btnColor={btnColorOption.secondary}
                onClick={handleSubmit}
              >
                Login
              </BtnPartial>
              <BtnPartial
                btnColor={btnColorOption.secondary}
                onClick={resetPasswordClick}
              >
                Reset
              </BtnPartial>
            </div>
          </div>
        </div>
      </form>
    </Fragment>
  );
};

export default LoginPage;
